.glassmorphism-container {
	display: flex;
  flex-direction: row;
	justify-content: center;
	align-items: center;
  background: rgba(255, 255, 255, 0.1);
}

.glassmorphism-card {
	position: relative;
	background: rgba(255, 255, 255, 0.1);
	border-radius: 40px;
  justify-content: center;
	align-items: center;
	border: 2px solid #f0f0f0;
	box-shadow: 0 4px 6px rgba(0, 0, 0, 0.41);
}

h1 {
	color: #333;
}

p {
	color: #666;
}