.blog-bg-image {
    width: 100%;
    height: 100vh;
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 0;
  }

.project-description-container{
    position: absolute;
    top: 25%;
    left: 10%;
    right: 10%;
    bottom: 25%;
    background-color: rgba(22, 22, 22, 0.2); /* Semi-transparent background for readability */
    padding: 20px;
    border-radius: 10px;
    z-index: 2; /* Higher z-index to place the form in front of the image */
    text-align: center;
    backdrop-filter: blur(10px);
    overflow: auto;
}

.form-container {
    position: absolute;
    height: auto;
    width: 50%;
  }

.form-container h1{
  color: #000;
}

.vertical-line {
    position: absolute;
    border-left: 2px solid rgb(255, 255, 255);
    left: 50%;
    height: 300px;
  }

.project-description {
    position: absolute;
    left: 50%;
    font-family: Arial, sans-serif;
    font-size: 16px;
    line-height: 1.6;
    margin: 20px;
    color: #f0f0f0;
    overflow-wrap: break-word; /* Ensure long words break correctly */
    word-wrap: break-word; /* Ensure long words break correctly */
    hyphens: auto;
}

.output-container p{
    position: absolute;
    margin-bottom: 20px;
    width: 80%;
    margin: 20px;
    color: #000;
}

.project-description-container a{
  position: absolute;
  margin: 20px;
  right: 10%;
  bottom: 10%;
}