body::-webkit-scrollbar{
  display: none;
}
.landingpage-parent {
  position: relative;
  width: 100%;
  height: 100%;
  scrollbar-width: none;
  overflow: -moz-scrollbars-none;
}
@keyframes scale-down-center {
0% {
  -webkit-transform: scale(1);
  transform: scale(1);
}
to {
  -webkit-transform: scale(0.5);
  transform: scale(0.5);
}
}
.background {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  background-color: var(--color-darkslategray-100);
  overflow: hidden;
}
.gradientbg-icon {
  position: absolute;
  height: 780px;
  width: 100%;
  top: 50px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
}
.logocontent{
  position: absolute;
  top: 50px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  width: 100%;
  height: 100%;
}
.setlogo-icon {
  position: absolute;
  top: 0px;
  left: calc(50% - 556.6px);
  width: calc(1336px/1.2);
  height: calc(888px/1.2);
  object-fit: cover;
}
.blurbg {
  position: absolute;
  top: 50px;
  left: 0px;
  background-color: var(--color-gainsboro);
  backdrop-filter: blur(27px);
  width: 1454px;
  height: 951px;
  display: none;
}
.desktop2 {
  position: absolute;
  align-items: center;
  width: 100%;
  height: 780px;
  overflow: hidden;
  z-index: 3;
}
.about {
  position: relative;
}
.about-frame {
  z-index: 3;
  position: absolute;
  top: 143px;
  left: 71px;
  width: 222px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: var(--padding-21xl);
  box-sizing: border-box;
  font-size: var(--font-size-41xl);
  font-family: var(--font-kaushan-script);
}
.aboutcontent {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 764px;
  flex-shrink: 0.4;
  z-index: 3;
}
.aboutcontent-wrapper {
  position: absolute;
  width: calc(100% - 876px);
  top: 310px;
  right: 605px;
  left: 71px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  flex-wrap: wrap;
  padding: var(--padding-21xl) 0px;
  box-sizing: border-box;
  font-size: var(--font-size-5xl);
  font-family: var(--font-caladea);
}
.aboutcontent-wrapper a{
  position: absolute;
  top: 100%;
  right: 56px;
  color: #fff;
  text-decoration: none;
  z-index: 3;
}
.aboutwhole {
  position: absolute;
  width: 100%;
  top: 780px;
  right: 0px;
  left: 0px;
  background-color: var(--color-black);
  height: 780px;
  overflow: hidden;
  text-align: center;
  font-size: var(--font-size-5xl);
  color: var(--color-white);
  font-family: var(--font-caladea);
  z-index: 1;
}
.profilepic{
  position: absolute;
  top: 160px;
  right:160px;
  height: 610px;
  width: 490px;
  display: block;
  z-index: 4;
}
.profilemask{
  position: absolute;
  top: 0px;
  right:0px;
  height: 780px;
  width: 660px;
  z-index: 4;
  box-shadow: 0 0 25px 25px black;
}
.particlescontainer{
  position: absolute;
  width: 100%;
  top: 0px;
  right: 0px;
  left: 0px;
  background-color: var(--color-black);
  height: 780px;
  overflow: hidden;
  z-index: 2;
}
.glassmorph{
  z-index: 6;
}
a.social{
  position: relative;
  padding: 14px;
  color: white;
  cursor: pointer;
  svg{
    opacity: 0.6;
    &:hover{
      filter: opacity(0.95) drop-shadow(0px 0px 4px var(--glow-color));
      -webkit-filter: opacity(0.95) drop-shadow(0px 0px 4px var(--glow-color));
      -ms-filter: opacity(0.95) drop-shadow(0px 0px 4px var(--glow-color));
      opacity: 1;
    }
  }
}
