}
.dummy-bg{
  position: absolute;
  width: 100%;
  color: #000000;
  background-color: hsl(0, 0%, 8%);
  overflow: hidden;
  scrollbar-width: none;
}
.dummy-bg button{
  position: absolute;
  cursor: pointer;
  overflow: hidden;
  color: #ff2222;
  border: none;
  background: linear-gradient(246deg, #FF4789 14.75%, #FF774B 86.87%);
}
.dummy-bg button span {
  position: absolute;
  width: 100px;
  font-size: medium;
  align-items: center;
}
#home-button{
  width: 400px;
